import {useState} from 'react';
import {Tab, Tabs} from '@blueprintjs/core';

const SecondaryTabs = props => {
  const [tab, setTab] = useState('activity');
  const handleTabChange = tab => {
    setTab(tab);
  };
  return (
    <Tabs
      id="GuildSecondaryTabs"
      className="tabs-guild-secondary"
      onChange={handleTabChange}
      selectedTabId={tab}>
      <Tab
        id="activity"
        title="Activity"
        panel={<Example title="Activity" />}
        panelClassName="activity-panel"
      />
      <Tab id="members" title="Members" panel={<Example title="Members" />} />
    </Tabs>
  );
};

const Example = props => {
  return (
    <Atom.Flex>
      <Atom.Heading>{props.title}</Atom.Heading>
    </Atom.Flex>
  );
};

export default SecondaryTabs;
