/* --- Component --- */
const DAOActivityList = ({activityList}) => {
  return (
    <Atom.Flex column>
      {activityList &&
        activityList.map(activity => <ActivityItem {...activity} />)}
    </Atom.Flex>
  );
};
export default DAOActivityList;

const ActivityItem = props => {
  return (
    <Atom.Flex
      alignCenter
      between
      sx={{
        borderBottom: '2px solid',
        borderColor: 'gray',
        py: 2,
        mb: 3,
      }}>
      <Atom.Flex alignCenter>
        <Atom.Image
          sx={{
            boxShadow: 0,
            border: '2px solid',
            borderColor: 'white',
            borderRadius: 9999,
            maxWidth: 32,
            mr: 3,
          }}
          src="https://pbs.twimg.com/profile_images/1221422581976268803/z6LUd57G_400x400.jpg"
        />
        <Atom.Span
          as="h5"
          sm
          sx={{
            color: 'charcoal',
            fontSize: [1, 1, 2],
          }}>
          {props.title}
        </Atom.Span>
      </Atom.Flex>
      <Atom.Box>
        <Atom.Span sm>{props.timestamp}</Atom.Span>
        <Atom.Span tag sm sx={{ml: 2}}>
          Details
        </Atom.Span>
      </Atom.Box>
    </Atom.Flex>
  );
};
