/* --- Global --- */
import idx from 'idx';
import {useState} from 'react';
import {Drawer} from '@blueprintjs/core';
import {Component} from '@horizin/ui-compose';

/* --- Local --- */
import {FormPraiseGeneric} from '@forms';
/* --- Component --- */
const PraiseCreateDrawer = ({componentActivate, ...props}) => {
  const [isOpen, setOpenStatus] = useState(false);
  const toggleDrawer = () => setOpenStatus(!isOpen);

  return (
    <>
      <Atom.Box>
        <Atom.Span onClick={toggleDrawer}>
          {componentActivate ? Component(componentActivate) : null}
        </Atom.Span>
      </Atom.Box>
      <Drawer
        title="Give Praise"
        icon="crown"
        isOpen={isOpen}
        onClose={toggleDrawer}
        size={420}
        position={{right: 0}}>
        <Atom.Box sx={{p: 3}}>
          <Atom.Paragraph>
            Send praise to{' '}
            <strong>
              {idx(props, _ => _.identity.name) ||
                idx(props, _ => _.identity.address) ||
                'a developer'}{' '}
            </strong>
            for being awesome.
          </Atom.Paragraph>
          <FormPraiseGeneric
            identity={props.identity}
            recipient={props.identity.address}
          />
        </Atom.Box>
      </Drawer>
    </>
  );
};

export default PraiseCreateDrawer;
