/* --- Global --- */
import {Position, Tooltip} from '@blueprintjs/core';

/* --- Component --- */
const CollectibleBadge = props => {
  return (
    <Atom.Box sx={styleCollectible}>
      <Tooltip
        content={`Collectible: ${props.name}`}
        position={Position.BOTTOM}>
        <Atom.Image src={props.image} sx={{maxWidth: 32}} />
      </Tooltip>
    </Atom.Box>
  );
};

const styleCollectible = {
  bg: 'white',
  boxShadow: 0,
  borderRadius: '20px 5px 20px 15px',
  cursor: 'pointer',
  mx: 1,
  p: '10px',
  '&:hover': {
    boxShadow: 2,
    transform: 'scale(1.025)',
  },
};

export default CollectibleBadge;
