/* --- Global --- */
import {Component} from '@horizin/ui-compose';

/* --- Component --- */
const CollectionList = ({collectionList, component, sx, ...props}) => {
  return (
    <Atom.Flex sx={sx} {...props}>
      {collectionList &&
        collectionList.map(collectible =>
          Component(component, {...collectible}),
        )}
    </Atom.Flex>
  );
};

export default CollectionList;
