import idx from 'idx';
import {useForm} from 'react-hook-form';

/* --- Component --- */
const FormRegistration = props => {
  const {handleSubmit, register, errors} = useForm();

  const onSubmit = values => {
    console.log(values);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} style={{width: '100%'}}>
      <Molecule.Field
        name="title"
        placeholder="Title"
        register={register}
        errors={errors}
        sx={styles.field}
      />

      <Molecule.Field
        as="textarea"
        name="description"
        placeholder="The idea is important because..."
        register={register}
        errors={errors}
        sx={{
          height: 120,
        }}
      />

      <Atom.Button md rounded sx={styles.button}>
        {props.label}
      </Atom.Button>
    </form>
  );
};

const styles = {
  field: {
    borderColor: 'gray',
    borderWidth: 1,
    borderStyle: 'solid',
    boxShadow: 0,
    p: 10,
    my: 1,
    width: '100%',
  },
  button: {
    mt: 2,
    width: '100%',
  },
};

FormRegistration.defaultProps = {
  label: 'Submit Idea',
};

export default FormRegistration;
