/* --- Global --- */
import {useState} from 'react';
import {Position, Tooltip} from '@blueprintjs/core';

/* --- Component --- */
const TokenBalance = ({address, symbol, name, sx, ...props}) => {
  const [balance, setBalance] = useState(0);

  return !symbol ? null : (
    <Atom.Flex alignCenter sx={{...sx, ...style.container}}>
      <Atom.Span>
        <Tooltip
          content={`Token: ${name}`}
          position={props.position || Position.LEFT}>
          <>
            <strong>
              <Molecule.Link
                to={`/token/${address}`}
                sx={{
                  '&:hover': {
                    color: 'inherit',
                  },
                }}>
                {symbol}:
              </Molecule.Link>
            </strong>{' '}
          </>
        </Tooltip>
        <Atom.Span sx={style.balance}>{balance}</Atom.Span>
      </Atom.Span>
    </Atom.Flex>
  );
};

const style = {
  container: {
    justifyContent: 'flex-end',
  },
  balance: {
    ml: 1,
  },
};

export default TokenBalance;
