import {useState} from 'react';
import {Tab, Tabs} from '@blueprintjs/core';

const MainTabs = props => {
  const [tab, setTab] = useState('bounties');
  const handleTabChange = tab => {
    console.log(tab, 'tabtab');
    setTab(tab);
  };
  return (
    <Tabs
      id="GuildTabs"
      className="tabs-guild-primary"
      onChange={handleTabChange}
      selectedTabId={tab}>
      <Tab
        id="bounties"
        title="Bounties"
        panel={<Example title="Bounties" />}
        panelClassName="bounties-panel"
      />
      <Tab
        id="challenges"
        title="Challenges"
        panel={<Example title="Challenges" />}
        panelClassName="challenge-panel"
      />
      <Tab
        id="Quests"
        title="Bounties"
        panel={<Example title="Quests" />}
        panelClassName="quests-panel"
      />
      <Tab
        id="rewards"
        title="Rewards"
        panel={<Example title="Reward" />}
        panelClassName="rewards-panel"
      />
    </Tabs>
  );
};

const Example = props => {
  return (
    <Atom.Flex>
      <Atom.Heading>{props.title}</Atom.Heading>
    </Atom.Flex>
  );
};

export default MainTabs;
