/* --- Global --- */
import {useState} from 'react';

/* --- Local --- */
import {CollectibleBadge} from '@components';
import GuildMenu from './GuildMenu';

/* --- Showcase : Style --- */

const styleShowcase = {
  bg: 'blue',
  color: 'white',
  transition: '0.2s all',
};

/* --- Showcase : Component --- */
const Showcase = props => {
  const [isExpanded, setIsExpanded] = useState(true);
  const [styleExpanded, setExpandedStyle] = useState({minHeight: [240]});

  const expanded = {
    minHeight: [240],
  };
  const minimized = {
    minHeight: [100],
  };

  const showcaseMinimize = () => {
    setIsExpanded(false);
    setExpandedStyle(minimized);
  };
  const showcaseExpand = () => {
    setIsExpanded(true);
    setExpandedStyle(expanded);
  };
  const toggleExpanded = () => {
    setIsExpanded(!isExpanded);
    setExpandedStyle(isExpanded ? minimized : expanded);
  };

  return (
    <Atom.Flex sx={{...styleShowcase, ...styleExpanded}}>
      <Atom.BackgroundImage ratio={0.4} src={props.cover} sx={{opacity: 0.5}} />
      {/* --- Top Left : Begin --- */}
      <FeatureChallengeView isExpanded={isExpanded} />
      {/* --- Top Left : Right --- */}

      <MenuAndExpandedView
        showcaseMinimize={showcaseMinimize}
        showcaseExpand={showcaseExpand}
        toggleExpanded={toggleExpanded}
        id={props.id}
      />

      {/* --- Bottom Left : Begin --- */}

      <TitleAndTaglineView
        title={props.title}
        subtitle={props.subtitle}
        image={props.image}
        isExpanded={isExpanded}
      />

      {/* --- Bottom Right : Begin --- */}
      <CollectibleView badges={props.badges} isExpanded={isExpanded} />
      {/* --- Bottom Right : End --- */}
    </Atom.Flex>
  );
};

const MenuAndExpandedView = props => {
  return (
    <Atom.Absolute
      sx={{
        top: 0,
        right: 0,
        m: 3,
      }}>
      <Atom.Flex alignCenter>
        <Atom.Span tag sm pointer sx={{mx: 2}} onClick={props.toggleExpanded}>
          Expanded
        </Atom.Span>
        <Molecule.Link to={`/guild/${props.id}/idea/create`}>
          <Atom.Span
            tag
            sm
            pointer
            sx={{mx: 2}}
            onClick={props.showcaseMinimize}>
            Idea Create
          </Atom.Span>
        </Molecule.Link>
        <GuildMenu>
          <Atom.Flex alignCenter card sx={{p: 2}}>
            <Atom.Image
              src="https://image.flaticon.com/icons/svg/149/149021.svg"
              sx={{width: 18}}
            />
            <Atom.Span sx={{ml: 2}}>MENU</Atom.Span>
          </Atom.Flex>
        </GuildMenu>
      </Atom.Flex>
    </Atom.Absolute>
  );
};

const TitleAndTaglineView = props => {
  return (
    <Atom.Absolute
      sx={{
        bottom: 0,
        left: 0,
        m: 3,
      }}>
      <Atom.Flex alignCenter>
        <Atom.Flex card center column p1>
          <Atom.Image src={props.image} sx={{width: 32}} />
        </Atom.Flex>
        <Atom.Box sx={{ml: 3}}>
          <Atom.Heading as="h2" xxl m0>
            {props.title}
          </Atom.Heading>
          <Atom.Heading as="h5" md thin m0>
            {props.subtitle}
          </Atom.Heading>
        </Atom.Box>
      </Atom.Flex>
    </Atom.Absolute>
  );
};

const CollectibleView = props => {
  return !props.isExpanded ? null : (
    <Atom.Absolute
      sx={{
        top: 0,
        left: 0,
        m: 3,
      }}>
      <Atom.Flex>
        {props.badges.map(badge => (
          <CollectibleBadge {...badge} />
        ))}
      </Atom.Flex>
      <Atom.Span sm sx={{mt: 1}}>
        {/* Collectibles */}
      </Atom.Span>
    </Atom.Absolute>
  );
};

const FeatureChallengeView = props => {
  return !props.isExpanded ? null : (
    <Atom.Absolute
      sx={{
        bottom: 0,
        right: 0,
        m: 3,
      }}>
      <Atom.Span sm>
        <strong>Featured Challenged:</strong>{' '}
        <Molecule.Link to={`/guild/${props.id}/challenge/${props.challengeId}`}>
          Prototype a new decentralized finance protocol.
        </Molecule.Link>
      </Atom.Span>
    </Atom.Absolute>
  );
};

export default Showcase;
