import {useState} from 'react';
import {Popover, PopoverInteractionKind, Position} from '@blueprintjs/core';

import {Tab, Tabs} from '@blueprintjs/core';

/* --- ProfileAvatar --- */
const ProfileAvatar = props => {
  return (
    <Atom.Box sx={props.sx}>
      <Popover
        interactionKind={PopoverInteractionKind.CLICK}
        popoverClassName="bp3-popover-content-sizing"
        position={Position.BOTTOM_LEFT}>
        <Atom.Box>
          <Atom.Image
            sx={{
              boxShadow: 0,
              border: '2px solid',
              borderColor: 'white',
              borderRadius: 9999,
              maxWidth: 40,
            }}
            src="https://pbs.twimg.com/profile_images/1221422581976268803/z6LUd57G_400x400.jpg"
          />
        </Atom.Box>

        <Atom.Box
          sx={{
            minWidth: 300,
          }}>
          <Atom.Box
            sx={{
              bg: 'smoke',
              boxShadow: 1,
              mb: 3,
              mt: -20,
              mx: -20,
              p: 3,
            }}>
            <Molecule.Link to="/profile">
              <Atom.Heading md as="h5">
                {props.identity.emoji} {props.identity.name}
                <Molecule.Link to={`/guild/${props.identity.guild.alias}`}>
                  <Atom.Span sx={{fontWeight: 300}}>
                    | {props.identity.guild.name}
                  </Atom.Span>
                </Molecule.Link>
              </Atom.Heading>
            </Molecule.Link>
            <Atom.Flex>
              <Atom.Span sm>
                <strong>{props.identity.role}</strong> on the{' '}
                <em>{props.identity.team}</em> team{' '}
              </Atom.Span>
            </Atom.Flex>
          </Atom.Box>

          <ProfileTabs activityList={props.activityList} />

          <Atom.Box
            gradient="blue"
            sx={{
              color: 'white',
              mb: -20,
              mx: -20,
              p: 3,
            }}>
            <Atom.Flex alignCenter sx={{justifyContent: 'flex-start'}}>
              <Atom.Span sx={{fontSize: 1, mr: 2}}>
                Level: {props.identity.meta.level}
              </Atom.Span>
              <Atom.Span sx={{fontSize: 0, mr: 2}}>
                Experience: {props.identity.meta.experience}
              </Atom.Span>
            </Atom.Flex>
          </Atom.Box>
        </Atom.Box>
      </Popover>
    </Atom.Box>
  );
};

const ProfileTabs = props => {
  const [selectedTabId, setSelectedTabId] = useState('activity');

  return (
    <Tabs
      id="TabsExample"
      onChange={setSelectedTabId}
      selectedTabId={selectedTabId}>
      <Tab
        id="activity"
        title="Activity"
        panel={<ActivityPanel activityList={props.activityList} />}
        panelClassName="activity-panel"
      />
      <Tab
        id="community"
        title="Community"
        panel={<CommunityPanel />}
        panelClassName="community-panel"
      />
      <Atom.HorizontalRule sx={{}} />
      <Tabs.Expander />
      {/* <input className="bp3-input" type="text" placeholder="Search..." /> */}
    </Tabs>
  );
};

const ActivityPanel = ({activityList}) => {
  return (
    <Atom.Flex column>
      {activityList &&
        activityList.map(activity => <ActivityItem {...activity} />)}
    </Atom.Flex>
  );
};

const ActivityItem = props => {
  return (
    <Atom.Flex
      between
      sx={{borderBottom: '2px solid', borderColor: 'gray', mb: 3}}>
      <Atom.Heading as="h6" sm sx={{color: 'charcoal'}}>
        {props.title}
      </Atom.Heading>
      <Atom.Span sm>{props.timestamp}</Atom.Span>
    </Atom.Flex>
  );
};

const CollectionsPanel = props => {
  return (
    <Atom.Flex>
      <Atom.Heading sm>Gear</Atom.Heading>
    </Atom.Flex>
  );
};

const CommunityPanel = props => {
  return (
    <Atom.Flex>
      <Atom.Heading sm>Community</Atom.Heading>
    </Atom.Flex>
  );
};

export default ProfileAvatar;
